import React, { useEffect } from 'react';
import useDrawStore from '../store/useDrawStore';
import { useNavigate } from 'react-router-dom';
import useAuthStore from '../store/useAuthStore';
import './css/admin.css';
import { Trash2 } from 'lucide-react';
import Swal from 'sweetalert2';

function AdminPage() {
    const {
        prizes,
        displayMode,
        isLocked,
        isClosed,
        addPrize,
        updatePrize,
        deletePrize,
        setDisplayMode,
        setLocked,
        setClosed,
        saveToFirebase,
        loadFromFirebase,
        listenToFirebase,
    } = useDrawStore();

    const isAdmin = useAuthStore((s) => s.isAdmin);
    const navigate = useNavigate();

    useEffect(() => {
        if (!isAdmin) {
            console.log('[Auth]    /admin-login ');
            navigate('/admin-login');
        } else {
            console.log('[Auth] ');
        }
    }, [isAdmin]);

    useEffect(() => {
        loadFromFirebase();
        listenToFirebase();
    }, []);

    return (
        <div className='admin'>
            <div className="admin-header">
                <h1>  <span>  /         .</span></h1>
                <div className="admin-status">
                    <button
                        className='btn-red'
                        onClick={async () => {
                            const result = await Swal.fire({
                                title: isClosed ? '  ?' : '  ?',
                                showCancelButton: true,
                                confirmButtonText: isClosed ? ' ' : '',
                                cancelButtonText: '',
                                confirmButtonColor: '#00c67e',
                            });

                            if (!result.isConfirmed) return;

                            const newClosed = !isClosed;
                            setClosed(newClosed);
                            await saveToFirebase();

                            await Swal.fire({
                                title: newClosed ? '.' : '  .',
                                confirmButtonColor: '#00c67e',
                            });

                            window.location.reload();
                        }}
                    >
                        {isClosed ? '' : ''}
                    </button>
                </div>
            </div>
            <div className="admin-table">
                <table border="1" cellPadding="8">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th> </th>
                            <th> </th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {prizes.map((prize, index) => (
                            <tr key={index}>
                                <td>{prize.rank}</td>
                                <td>
                                    <input
                                        type="text"
                                        value={prize.name}
                                        disabled={isLocked}
                                        onChange={(e) => updatePrize(index, { name: e.target.value })}
                                    />
                                </td>
                                <td>
                                    <input
                                        type="number"
                                        value={prize.quantity}
                                        disabled={isLocked}
                                        onChange={(e) => {
                                            const quantity = parseInt(e.target.value);
                                            updatePrize(index, {
                                                quantity,
                                                remaining: quantity,
                                            });
                                        }}
                                    />
                                </td>
                                <td>{prize.remaining}</td>
                                <td>
                                    <button
                                        className='delete-prize'
                                        onClick={async () => {
                                            const result = await Swal.fire({
                                                title: `${prize.rank}  "${prize.name}" ?`,
                                                showCancelButton: true,
                                                confirmButtonText: '',
                                                cancelButtonText: '',
                                                confirmButtonColor: '#00c67e',
                                            });
                                            if (result.isConfirmed) {
                                                deletePrize(index);
                                                Swal.fire({
                                                    title: '.',
                                                    text: '(  )',
                                                    confirmButtonColor: '#00c67e',
                                                });
                                            }
                                        }}
                                        disabled={isLocked}
                                    >
                                        <Trash2 size={20} strokeWidth={1.5} />
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
            <div className="admin-btn-wrapper">
                <button className='lock-prize btn-white' onClick={() => setLocked(!isLocked)}>
                    {isLocked ? '' : ''}
                </button>
                <button className='add-prize btn-white' onClick={addPrize} disabled={isLocked || prizes.length >= 10}>
                    
                </button>
            </div>
            <div className='admin-wrapper'>
                <div className="admin-row">
                    <h2>  </h2>
                    <label>
                        <input
                            type="radio"
                            value="rank"
                            checked={displayMode === 'rank'}
                            disabled={isLocked}
                            onChange={(e) => setDisplayMode(e.target.value)}
                        /> 
                    </label>
                    <label>
                        <input
                            type="radio"
                            value="prize"
                            checked={displayMode === 'prize'}
                            disabled={isLocked}
                            onChange={(e) => setDisplayMode(e.target.value)}
                        /> 
                    </label>
                    <label>
                        <input
                            type="radio"
                            value="both"
                            checked={displayMode === 'both'}
                            disabled={isLocked}
                            onChange={(e) => setDisplayMode(e.target.value)}
                        />  
                    </label>
                </div>
            </div>
            <div className="admin-footer">
                <button
                    className='btn-mint'
                    onClick={async () => {
                        const result = await Swal.fire({
                            title: '?',
                            showCancelButton: true,
                            confirmButtonText: '',
                            cancelButtonText: '',
                            confirmButtonColor: '#00c67e',
                        });
                        if (!result.isConfirmed) return;

                        await saveToFirebase();
                        await Swal.fire({
                            title: '!',
                            confirmButtonColor: '#00c67e',
                        });
                        window.location.reload();
                    }}
                >
                    
                </button>
            </div>
        </div>
    );
}

export default AdminPage;
